const Curriculum = () => {
  const renderTree = (sessions) =>
    sessions.map((session) => (
      <TreeView id="treeView" multiselect={false} size="default">
        <TreeView.TreeNode
          key={session.title}
          isExpanded={false}
          label={<h6 className="sessionTitle">{session.title}</h6>}
          value={session.title}
        >
          {session.sections.map((section) => (
            <TreeView.TreeNode
              key={section}
              isExpanded={false}
              label={section}
              value={section}
            />
          ))}
        </TreeView.TreeNode>
      </TreeView>
    ));

  return (
    <div id="curriculum">
      <h5>Curriculum</h5>
      <div id="treeContainer">{renderTree(curriculum)}</div>
    </div>
  );
};

